<template>
  <div class="container">
    <b-container fluid class="main">
      <b-card >
        <h1>Hora Ideal de Estudo e Descano</h1>
        <b-row>
          <b-col>
            <img src="../assets/hora.jpg" img-alt="Image">
          </b-col>
          <b-col>
            <b-card-text>Sabe quando voc tem uma prova ou apresentao e fica o dia todo estudando de 5 em 5 minutos? Quando voc faz isso  muito mais fcil que no momento em que voc for usar aquele contedo voc o esquea. Isso se explica porque quando se estuda de forma picada, voc no consegue realmente consolidar o contedo em sua cabea.</b-card-text>
          </b-col>
          <b-col>
            <b-card-text>Para que o que voc est estudando realmente se fixe em sua cabea,  necessrio que seja definido um momento longo em seu dia dedicado ao estudo, isso far com que o contedo se consolide em sua memria. O momento em que voc realizar essa atividade poder variar de acordo com a sua rotina, mas  importante que no seja em um momento em que voc tenha muito sono ou que tenha muito barulho ao seu redor.</b-card-text>
          </b-col>
          <b-col>
            <b-card-text>Contudo, mesmo assim  importante que voc faa um intervalo para descansar. E claro, sempre ter uma boa noite de sono e uma boa alimentao, j que esses dois fatores afetam a nossa aprendizagem.</b-card-text>
          </b-col>
        </b-row>
      </b-card>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "gardner"
};
</script>

