<template>
  <div class="container">
    <div class="main" >
      <b-card border-variant="light"  header="Gardner">
        <b-tabs content-class="mt-3" variant="dark">
          <b-tab title="Biografia" active>
            <b-row>
              <b-col>
                <img src="../assets/Howard.jpg" img-alt="Image">
              </b-col>
              <b-col>
                <p>Howard Gardner nasceu em 1943, foi psiclogo e pesquisador da universidade norte-americana de Harvard e tambm o autor da Teoria das Inteligncias Mltiplas.</p>
              </b-col>
            </b-row>
          </b-tab>
          <b-tab title="Teoria da inteligncias Mltiplas">
            <b-row>
              <b-col>
                <img src="../assets/download.jpg" img-alt="Image">
              </b-col>
              <b-col>
                <p>Voc j pensou que diversas pessoas que so timos alunos durante a escola podem no obter sucesso em sua vida? Ou o contrrio, pessoas que fracassaram em sua vida acadmica podem obter grande xito no decorrer da sua vida?</p>
                <p>Howard Gardner percebeu exatamente isso e comeou a desenvolver uma pesquisa, para sanar sua curiosidade do porqu desse fenmeno. Ao fim dessa pesquisa, concluiu que as tcnicas usadas na escola no eram eficientes, pois estas levavam em considerao apenas a Teoria do QI, de Alfred Binet, a qual demonstrou ser uma teoria pouco abarcadora, uma vez que muitas pessoas no se encaixavam nessa teoria. Dessa forma, Gardner criou a Teoria das Inteligncias Mltiplas, na qual afirma que as pessoas possuem diversos tipos de inteligncias ou dons.</p>
              </b-col>
              <b-col>
                <p>A inteligncia, para Gardner,  a capacidade de resolver problemas ou elaborar produtos valorizados em um ambiente cultural ou comunitrio. A partir dessa definio, ele identificou oito tipos de inteligncias, as quais, segundo o terico, no representam um nmero final e definitivo.</p>
                <p>Gardner classificou as inteligncias em: inteligncia lingustica, inteligncia lgico-matemtica, inteligncia espacial, inteligncia musical, inteligncia cinestsica, inteligncia interpessoal e intrapessoal, inteligncia corporal cinestsica, inteligncia naturalista e, inteligncia existencial. Segundo o autor, os seres humanos possuem essas inteligncias em quantidades variadas, e cada um as combina de formas diferentes.</p>
              </b-col>
            </b-row>
          </b-tab>
          <b-tab title="Gardner em Sala de Aula">
            <b-row>
              <b-col>
                <img src="../assets/Sala.jpg" img-alt="Image">
              </b-col>
              <b-col>
                <p>A partir a teoria de Gardner,  possvel mudar toda a aprendizagem, pois  possvel compreender que as pessoas possuem diferentes inteligncias e por isso diferentes modos de aprender.</p>
                <p>Assim, Gardner sugere mudanas com relao ao desenvolvimento de avaliaes e  educao. Hoje, a avaliao  tida como um produto do aprendizado, quando, no entanto, esta deve ser parte do processo de aprendizado e deveria ser realizada das formas mais variadas possveis para que todos sejam beneficiados.</p>
                <p>No que se refere  educao, Gardner chama a ateno para o fato de que, as escolas devem preparar seus alunos para a vida. O que s ser possvel se os alunos forem encorajados a usar seu conhecimento para solucionar problemas reais. O aluno deve ser encorajado a desenvolver suas habilidades individuais para solucionar os problemas que a ele forem apresentados.</p>
              </b-col>
            </b-row>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "gardner"
};
</script>

<style>
.main {
  text-align: justify;
}
a {
  color: rgb(61, 60, 60);
}
a:hover {
  color: black;
}
.nav-tabs .nav-link {
  border-color: #fff;
}
.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
  background-color: transparent;
}
</style>



