<template>
  <div class="container">
    <b-container fluid class="main">
      <b-card >
        <h1>Problemas de Memria</h1>
        <b-row>
          <b-col>
            <img src="../assets/memoria.jpg" img-alt="Image">
          </b-col>
          <b-col>
            <b-card-text>Alguma vez voc estava fazendo a prova e te deu um branco? Ou, talvez, no meio de uma apresentao voc esqueceu qual era sua fala? Quando isso acontece no  porque voc estudou ou no treinou. O problema  que voc apenas decorou a matria e no a entendeu.</b-card-text>
          </b-col>
          <b-col>
            <b-card-text>A primeira soluo para essas situaes  tentar reter em sua memria palavras chave que te lembrem o contedo. A segunda  aprender realmente, e no apenas superficialmente, ou seja, no apenas decorando coisas, pois isso no  eficaz. </b-card-text>
          </b-col>
          <b-col>
            <b-card-text>Para que voc consiga isso, pode diante de um espelho, explicar diversas vezes, at que voc fale sobre o contedo de forma natural, j que isso significa que voc aprendeu o contedo. A ltima dica  buscar que o contedo faa sentido para voc. Ento busque entend-lo, criando uma lgica para esse contedo. Assim, quando voc precisar desse contedo essa lgica te ajudar a lembrar.</b-card-text>
          </b-col>
        </b-row>
      </b-card>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "gardner"
};
</script>

