<template>
  <div class="container">
    <b-container fluid class="main">
      <b-card >
        <h1>Lugar Ideal</h1>
        <b-row>
          <b-col>
            <img src="../assets/lugar.jpg" img-alt="Image">
          </b-col>
          <b-col>
            <b-card-text>Onde voc estuda? Em cima da sua cama? Na sala de estar com sua famlia ao redor? Voc estuda ao mesmo tempo que conversa com seus amigos? Muitas vezes, a dificuldade em aprender a estudar  por causa do lugar em que voc se encontra. Quando estamos na cama, este  um lugar muito confortvel e favorvel ao sono, quando estamos no cho  um lugar muito desconfortvel para o nosso corpo.</b-card-text>
          </b-col>
          <b-col>
            <b-card-text>Ao se encontrar no meio de barulho a sua concentrao no fica no contedo, mas sim no que est acontecendo ao seu redor, e isso afetar sua aprendizagem. Manter-se organizado  extremamente importante tambm, pois mesa arrumada reflete uma cabea organizada.</b-card-text>
          </b-col>
          <b-col>
            <b-card-text>Contudo, mesmo assim  importante que voc faa um intervalo para descansar. E claro, sempre ter uma boa noite de sono e uma boa alimentao, j que esses dois fatores afetam a nossa aprendizagem.</b-card-text>
          </b-col>
        </b-row>
      </b-card>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "gardner"
};
</script>

<style>
.main {
  text-align: justify;
}
</style>



